$color-bg: #e4ebf5;

$color-default: #222;
$color-red: #d44439;
$color-gray: #666;
$color-light-gray: #999;
$color-deep-white: #e4e4e4;
$color-gold: #bba8a8;
$color-primary: #2979ff;
$color-primary-light: #82b1ff;

$font-size-default: 14px;
$font-size-max: 24px;
$font-size-bigger: 20px;
$font-size-large: 18px;
$font-size-normal: 16px;
$font-size-mini: 12px;

$padding-window: 20px;

@mixin fade {
  &:global(.fade-enter) {
    opacity: 0;
    transform: translateX(100%);
  }

  &:global(.fade-enter-active) {
    opacity: 1;
    transform: translateX(0);
    transition: all 500ms;
  }

  &:global(.fade-exit) {
    opacity: 1;
    transform: translateX(0);
  }

  &:global(.fade-exit-active) {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 500ms;
  }
}

@mixin fly {
  &:global(.fly-enter),
  &:global(.fly-appear) {
    transform: translate3d(100%, 0, 0);
  }
  &:global(.fly-enter-active),
  &:global(.fly-appear-active) {
    transition: transform 0.3s;
    transform: translate3d(0, 0, 0);
  }
  &:global(.fly-exit-active) {
    transition: transform 0.3s;
    transform: translate3d(100%, 0, 0);
  }
}

.wrapper-content {
  overflow: hidden;
  width: 1050px;
  padding: 5px;
  .card-wrapper {
    color: $color-gray;
    float: left;
    margin-right: 10px;
    width: 160px;
    white-space: pre-wrap;
    .img-card {
      padding: 15px;
      .img {
        border-radius: 0.25rem;
        display: block;
        width: 130px;
        height: 130px;
      }
    }
    .desc {
      padding-top: 5px;
      font-size: small;
    }
  }
}
